import React, { Component } from 'react';
import './App.css';
import $ from "jquery";
import { constants } from 'http';
class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = { name: "",
        email:'',
        letter:'',
     };

        this.onChange = this.onChange.bind(this);
        this.email = this.email.bind(this);
        this.letter = this.letter.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    onChange(e) {
        var val = e.target.value;
        this.setState({ name: val });
    }
email(e) {
        var val = e.target.value;
        this.setState({ email: val });
    }
letter(e) {
        var val = e.target.value;
        this.setState({ letter: val });
    }

    handleSubmit(e) {
   
        e.preventDefault();
        alert(": " + this.state.name + "email: " + this.state.email+ ": " + this.state.letter);
        const email = this.state.email
        const text = this.state.letter
        const name = this.state.name

        };
       



    render() {

  
    
        return (
                <section className='App__4st-slide'>
                    <div className='App__4st-slide__tree'>
                    <form onSubmit={this.handleSubmit}  >
                        <p>
                            <label>:</label><br />
                            <input type="text" value={this.state.name} onChange={this.onChange} />
                        </p>
                        <p>
                            <label> e-mail</label><br />
                            <input type="text" value={this.state.email} onChange={this.email} />
                        </p>
                        <p>
                            <label>edt</label><br />
                            <input type="text" value={this.state.letter} onChange={this.letter} />
                        </p>
                        <input type="submit" value="" />
                    </form>
                    </div>
                </section>
        );
    }
}

export default Contact;
